/* ==========================================================================
   WooCommerce Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape) and (max-width: $laptop-landscape-large-plus-pixel) {

    .widget_price_filter {
        .price_slider_amount {

            .button {
                float: left !important;  /* Position of price filter on ipad */
                padding: 7px 18px !important;
             }

            .price_label {
                float: left;
                margin-left: 15px;
            }
        }
    }
}


/* ==========================================================================
   WooCommerce Responsive styles - end
